.collapse_about {
  display: flex;
  flex-direction: column;
  padding: 0px 6%;
  gap: 30px;
  margin-top: 38px;

  @media screen and (max-width: 1024px) {
    padding: 3%;
    gap: 25px;
    margin-top: 24px;
  }

  @media screen and (max-width: 768px) {
    padding: 0px;
    gap: 20px;
    margin-top: 19px;
  }
}
